import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ViewMotortypeComponent } from './view-motortype/view-motortype.component';

const routes: Routes = [
  {
    path:'',
    component:ViewMotortypeComponent
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule] 
})
export class MotortypeRoutingModule { }
