import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { API_ENDPOINTS, API_URL } from 'src/app/Model/Global';
import { MotorType } from 'src/app/Model/MotorType';
import { BaseApiHandlerService } from '../base-api-handler.service'; 

@Injectable({
  providedIn: 'root'
})
export class MotorTypeService extends BaseApiHandlerService {
motortypeObserve$:Observable<MotorType[]>;
private motortypeSubject$: BehaviorSubject<MotorType[]> = new BehaviorSubject<MotorType[]>(undefined);

public get motortypes(){ return this.motortypeSubject$.value}
  constructor(private http:HttpClient) {
    super(http);
    this.motortypeObserve$= this.motortypeSubject$.asObservable();
  }
  SaveMotortypes(data){
    return this.Post(data,API_URL+API_ENDPOINTS.MotorType);
  }
  UpdateMotortypes(data:MotorType){
    return this.Update(data.id,data,API_URL+API_ENDPOINTS.MotorType);
  }
  GetMotortypes(){
    return this.GetAll(API_URL+API_ENDPOINTS.MotorType).subscribe(res =>{
      this.motortypeSubject$.next(res.dynamicResult);
    });
  }
  DeleteMotortypes(id){
    return this.Delete(id,API_URL+API_ENDPOINTS.MotorType);
  }
}
