import { Component, Input, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Subject } from 'rxjs';
import { MotorType } from 'src/app/Model/MotorType';
import { AlertService } from 'src/app/services/alert.service';
import { MotorTypeService } from 'src/app/services/motortype/motor-type.service';

@Component({
  selector: 'app-detailview-motortype',
  templateUrl: './detailview-motortype.component.html',
  styleUrls: ['./detailview-motortype.component.css']
})
export class DetailviewMotortypeComponent implements OnInit {
  @Input() motorTypeDataSubject:Subject<MotorType>;
form:FormGroup;
MotorTypeId:number;
isEditMode=false;
loading=false;    

  constructor(private fb:FormBuilder,
    private _notification:AlertService,
    private _motorService:MotorTypeService
    ) {
    
    this.form=fb.group({
      name:[null, [Validators.required]]
    });
   }
get f() {return this.form.controls}
  ngOnInit(): void {
this.motorTypeDataSubject.subscribe(res =>{
  if(res){
this.form.patchValue(res);
this.MotorTypeId=res.id;
this.isEditMode=false;
  }
  else{
    this.form.reset();
    this.MotorTypeId=null;
    this.isEditMode=false;
  }
});

  }
  handleSubmit(){
    if(this.form.invalid){
      return
    }
    this.loading=true;
    let data = this.form.value as MotorType;
    if(this.isEditMode){
      data.id=this.MotorTypeId;
      this._motorService.UpdateMotortypes(data).subscribe(res=>{
        this.loading=false;
        if(res.isSuccessfull){
          this.resetForm();
          this._motorService.GetMotortypes();
          this._notification.success('MotorType Update successfully!');
        }
        else{
          this._notification.error(' Error While Updating MotorType!');
        }
      });
    }
  else{
    this.loading=false;
    this._motorService.SaveMotortypes(data).subscribe(res=>{
      if(res.isSuccessfull){
        this.resetForm();
        this._motorService.GetMotortypes();
        this._notification.success('MotorType Save successfully!');
      }
      else{
        this._notification.error(' Error While Saving MotorType!');
      }

      });
    }
  }
  
  
  resetForm(){
    this.form.setValue({
      name:null
    });
  }

}
