import { Component, OnInit } from '@angular/core';
import { Subject, Subscription } from 'rxjs';
import { MotorType } from 'src/app/Model/MotorType';
import { AlertService } from 'src/app/services/alert.service';
import { MotorTypeService } from 'src/app/services/motortype/motor-type.service';

@Component({
  selector: 'app-view-motortype',
  templateUrl: './view-motortype.component.html',
  styleUrls: ['./view-motortype.component.css']
})
export class ViewMotortypeComponent implements OnInit {
  search: string;
  listData: MotorType[] = [];
  isvisible = false;
  modalTitle = "Create new Motor Type";
  MotorTypeObserSubject: Subject<MotorType> = new Subject();
  contactSubject: Subscription;
  checked = false;
  showMotorType = false;
  motorType = new MotorType();
  constructor(private _motorService: MotorTypeService,
    private _notification: AlertService) { }

  ngOnInit(): void {
    this._motorService.motortypeObserve$.subscribe(res => {
      if (res) {
        this.listData = res;
      }
      this.isvisible = false;
    });
    this._motorService.GetMotortypes();
  }
  // ngOnDestroy() {
  //   this.contactSubject.unsubscribe();
  // }
  handleCancel() {
    this.isvisible = false;
    this.showMotorType = false;
  }
  handleOpen() {
    this.isvisible = true;
    this.modalTitle = "New MotorType";
    this.MotorTypeObserSubject.next(null);
    this.showMotorType = false;
  }
  handleEdit(data: MotorType) {
    this.modalTitle = "Edit MotorType";
    this.isvisible = true;
    this.showMotorType = false;
    this.MotorTypeObserSubject.next(data);
  }
  showSingleMotorType(data: MotorType) {
    this.motorType = data;
    this.showMotorType = true;

  }
  DeleteMotorType(data: MotorType) {
    this._notification.delete().then(result => {
      if (result.isConfirmed) {
        this._motorService.DeleteMotortypes(data.id).subscribe(res => {
          if (res.isSuccessfull) {
            this._motorService.GetMotortypes();
            this._notification.success('MotorType Deleted sucessfully!')
          }
          else{
            this._notification.error('Error while Deleteing MotorType!')
          }
        });
      }
      else{
        return;
      }
    });
    this.showMotorType=false;

  }
}
